<script>
import { insertFirm, updataFirm } from '@/service/home'
import { firmInfo } from '@/service/customer'
// import { mapState, Store } from 'vuex'
import {
  formatDate, isNull, checkPhoneNum, checkEmail,
} from '@/utils'

export default {
  name: 'AddCustomer',
  data() {
    return {

      customerProfile: -1,
      showMoreBox: false,
      selectStartTime: false,
      createTime: '',
      maxDate: new Date(),
      minDate: new Date(1900, 1, 1),
      currentTime: new Date(),
      // formData
      form: {
        companyName: '', // 公司名称
        companyAdress: '',
        companyIndustry: '',
        companyCreditQuota: '',
        companyCustomerType: '',
        companyInitialFundingRequirements: '',
        companyUnifiedCreditCode: '',
        companyRegisteredCapital: '',
        companyEstablishmentTime: '',
        companyBankAccount: '',
        enterpriseActualController: '',
        enterpriseCorporateRepresentative: '',
        enterpriseCorporateIdCard: '',
        companyAbbreviation:'',
        companyBusinessLicense:'',
        openingAccountInformation:'',
        documentType:'',
        contactsFixedTelephone:'',
      },
      fuLiContactsInfoDTOS: [{
        contactsFirsted: '1',
        contactsSecond: '0',
        contactsName: '',
        contactsPost: '',
        contactsMoblie: '',
        contactsEmail: '',
        contactsIdentification:'第一联络人'
      }, {
        contactsFirsted: '0',
        contactsSecond: '1',
        contactsName: '',
        contactsPost: '',
        contactsMoblie: '',
        contactsEmail: '',
        contactsIdentification:'第二联络人'
      }],
      duty: '',
      mobile: '',
      butTxt: '新建完成',
    }
  },
  computed: {

  },
  watch: {},
  destroyed() {
    this.$store.commit('SET_UserType', false)
  },
  created() {
    if(this.$route.query.id){
      this.$store.commit('SET_UserType',true) 
      this.getData()
    }
  },
  methods: {
    formatter(type, value) {
      if (type === 'year') {
        return `${value}年`
      } if (type === 'month') {
        return `${value}月`
      }
      return `${value}日`
    },
    showMore() {
      this.showMoreBox = !this.showMoreBox
    },
    startTimeClick() {
      this.selectStartTime = true
    },
    startconfirm(e) {
      // 选开始时间
      if (isNull(e)) {
        this.selectStartTime = false
      } else {
        const time = formatDate(new Date(e), 'yyyy-MM-dd', 'special')
        const timeStr = time.split('-')
        this.form.companyEstablishmentTime = `${timeStr[0]}-${timeStr[1]}-${timeStr[2]}`
        this.selectStartTime = false
      }
    },
    // addBut
    addBut() {
      const that = this
      const params = this.form
      params.fuLiContactsInfoDTOS = this.fuLiContactsInfoDTOS
      delete params.companyId
      let txts = {                                                        
        companyCustomerType:'客户类别',
        companyCreditQuota:'授信额度',
        companyAdress:'地址',
        companyIndustry:'行业',
        companyAbbreviation:'客户简称',
        companyName:'客户名称',
        companyBusinessLicense:'统一社会信用代码',
      } 
      if ( !this.$checkFrom(params,txts))return
      // this.$Clog(params)
      if(isNull(params.fuLiContactsInfoDTOS[0].contactsName)){
        that.$notify('第一联系人姓名不能为空')
        return
      }
      if(isNull(params.fuLiContactsInfoDTOS[0].contactsPost)){
        that.$notify('第一联系人职务不能为空')
        return
      }
      if (!checkPhoneNum(params.fuLiContactsInfoDTOS[0].contactsMoblie)) {
        that.$notify('请确认第一联系人手机号码')
        return
      }
      if (!checkEmail(params.fuLiContactsInfoDTOS[0].contactsEmail)) {
        that.$notify('请确认第一联系人邮箱')
        return
      }
      if (this.$route.query.id) {
        this.updated()
      } else {
        this.inserted()
      }
      
    },
    // 新增企业
    inserted() {
      const that = this
      const params = this.form
      let addType = true

      params.fuLiContactsInfoDTOS = this.fuLiContactsInfoDTOS
      delete params.companyId

      // this.$Clog(params)
      if(isNull(params.fuLiContactsInfoDTOS[0].contactsName)){
        that.$notify('第一联系人姓名不能为空')
        addType = false
        return
      }
      if(isNull(params.fuLiContactsInfoDTOS[0].contactsPost)){
        that.$notify('第一联系人职务不能为空')
        addType = false
        return
      }
      if (!checkPhoneNum(params.fuLiContactsInfoDTOS[0].contactsMoblie)) {
          that.$notify('第一联系人手机号码有误')
          addType = false
          return
        }
        if (!checkEmail(params.fuLiContactsInfoDTOS[0].contactsEmail)) {
          that.$notify('第一联系人邮箱邮箱有误')
          addType = false
        }

      let txts = {
        companyName:'客户名称',
        companyIndustry:'行业',
        companyAdress:'地址',
        companyCreditQuota:'授信额度',
        companyCustomerType:'客户类别',
        companyAbbreviation:'客户简称',
        companyBusinessLicense:'营业执照'
      } 
      if (addType && this.$checkFrom(params,txts)) {
        insertFirm(params).then((res) => {
          if (res.data) {
            this.$notifySty('操作成功')
            this.$router.go(-1)
          } else {
            this.$notify(res.message)
          }
        })
      }
      
    },
    // 修改企业
    updated() {
      const params = this.form
      delete params.createTime
      delete params.updateTime
      delete params.leadingOfficialUserId
      params.fuLiContactsInfoDTOS = this.fuLiContactsInfoDTOS
      // this.$Clog(params);
      if (this.$checkFrom(params)) {

        updataFirm(params).then((res) => {
          this.$notifySty('操作成功')
          this.$router.go(-1)
        })
      }
    },
    getData() {
      const params = {
        id: this.$route.query.id,
      }
      firmInfo(params).then((res) => {
        this.tabData = res.data
        // console.log('企业信息firmInfo',res)
        this.form = res.data
        this.fuLiContactsInfoDTOS = res.data.fuLiContactsInfoVO
        
        let DArr = this.fuLiContactsInfoDTOS
        DArr = DArr || []
        let one = null
        let two = null
        let UserArr = []

        DArr.map((d, idx) => {   
          d.contactsIdentification = d.contactsIdentification||''       
          if (d.contactsIdentification.indexOf('第一')>-1) {
            one=d
          }
          if (d.contactsIdentification.indexOf('第二')>-1) {
            two=d
          }
          if (d.contactsIdentification.indexOf('第二') == -1&&d.contactsIdentification.indexOf('第一') == -1) {
            UserArr.push(d)
          }
          return d
        })   
        if(two){
          UserArr.unshift(two)
        } 
        if(one){
          UserArr.unshift(one)
        }        
        this.fuLiContactsInfoDTOS = UserArr
        // console.log(this.fuLiContactsInfoDTOS);
        this.butTxt = '提交更新'
      })
    },    
  },
}
</script>
